.wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.input {
  color: red;
  font-size: 1em;
  text-align: right;
}

.row {
  display: flex;
  height: 15%;
  flex: 1;
}

button {
  font-size: 1em;
  font-weight: bold;
  width: 25%;
  margin-top: 0.1em;
  margin-left: 0.05em;
  margin-right: 00.05em;
}

@media (min-width: 400px) and (min-height: 400px) {
  html {
    font-size: 2em;
  }
}

@media (min-width: 500px) and (min-height: 500px) {
  html {
    font-size: 3em;
  }
}

@media (min-width: 600px) and (min-height: 600px) {
  html {
    font-size: 4em;
  }
}

@media (min-width: 800px) and (min-height: 800px) {
  html {
    font-size: 5em;
  }
}
